<template>
<!--  -->
  <div>
    <!-- 头部 -->
    <header class="fl-ex al-it">
      <router-link to="/volunteeres" tag="img" src="../../../static/img/29.png" alt="图"></router-link>
      <p>志愿者管理</p>
    </header>
    <!-- 工会信息 -->
    <main v-for="(item,index) in list" :key="index">
      <div class="chairman">
        <div class="list" v-for="(s,idx) in item.lists" :key="idx">
          <div class="fl-ex al-it ju-be">
            <p>{{s.p}}</p>
            <span>{{s.span}}</span>
          </div>
        </div>
        <div style="width:100%; background-color:white;" v-for="(m,index) in item.img" :key="'info2-'+index">
          <div class="fl-ex al-it ju-be">
            <p>{{m.p}}</p>
            <span>{{m.span}}</span>
          </div>
          <img :src="m.pic" />
        </div>
      </div>
    </main>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [
        {
          lists: [
            {
              p: "志愿者姓名",
              span: "张红磊"
            },
            {
              p: "性别",
              span: "男"
            },
            {
              p: "民族",
              span: "汉"
            },
            {
              p: "政治面貌",
              span: "群众"
            },
            {
              p: "联系方式",
              span: "18870775656"
            },
            {
              p: "专业技能",
              span: "英语，计算机"
            },
            {
              p: "志愿者编号",
              span: "LM8945612"
            },
            {
              p: "从业日期",
              span: "2019-02-06"
            },
            {
              p: "所属机构",
              span: "雨滴筹"
            },
            {
              p: "单位",
              span: "西宁市钢铁厂"
            }
          ],
          img: [
            {
              p: "申请表",
              span: "",
              pic: "../../../static/img/46.png"
            }
          ]
        },
      ]
    };
  }
};
</script>

<style scpoed>
header {
  width: 100%;
  height: 3.2rem;
  background-color: #df0001;
}
header img {
  width: 4%;
  height: 1.25rem;
  margin-left: 5%;
}
header p {
  font-size: 1.2rem;
  color: white;
  margin-left: 28.7%;
}
/* 工会信息  */
main {
  width: 100%;
}
main .chairman {
  width: 100%;
  background-color: #e2e2e2;
}
main .chairman img {
  width: 30%;
  height: 10rem;
  margin-left: 3%;
  margin-top: 5%;
  padding-bottom: 3%;
}
main .chairman .list {
  width: 100%;
  height: 2.5rem;
  background-color: white;
}
main .chairman:last-child {
  padding-bottom: 0.5rem;
}
main .chairman > div > div {
  width: 94%;
  height: 2rem;
  margin: 0 auto;
  border-bottom: 0.05rem solid #e2e2e2;
}
main .chairman > div > div p {
  font-size: 0.8rem;
  color: #8c8c8c;
}
main .chairman > div > div span {
  font-size: 0.8rem;
  color: #393939;
}
</style>


